<template>
  <section class="msite">
    <!---->
    <Header :title="address.name || '...'">
      <span class="header_search" slot="left">
        <i class="iconfont icon-sousuo"></i>
      </span>
      <span class="header_login" slot="right">
        <span class="header_login_text">|</span>
      </span>
    </Header>
    <!---->
    <nav class="msite_nav">

      <div class="swiper-container" v-if="categorys.length>0">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(categorys, index) in categorysArr" :key="index">
            <a href="javascript:" class="link_to_food" v-for="(c, index) in categorys" :key="index">
              <div class="food_container">
                <img :src="'https://fuss10.elemecdn.com' + c.image_url">
              </div>
              <span>{{c.title}}</span>
            </a>
          </div>

        </div>

        <div class="swiper-pagination"></div>
      </div>

      <img src="./images/msite_back.svg" alt="loading" v-else>

    </nav>
    <!---->
    <ShopList/>
  </section>
</template>
<script>
  import {mapState} from 'vuex'
  import Swiper from 'swiper/dist/js/swiper'
  import 'swiper/dist/css/swiper.css'
  import ShopList from 'components/ShopList/ShopList.vue'

  export default {
    // 
    mounted () {

      this.$store.dispatch('getShops')
      this.$store.dispatch('getCategorys', () => { // , categorys
        //  DOM  DOM 
        this.$nextTick(() => {
          new Swiper('.swiper-container', { // 
            loop: true, // 
            // 
            pagination: {
              el: '.swiper-pagination',
            },
          })
        })
      })

      // swiper: 
      /*setTimeout(() => {
        new Swiper('.swiper-container', { // 
          loop: true, // 
          // 
          pagination: {
            el: '.swiper-pagination',
          },
        })
      }, 1000)*/
    },

    computed: {
      // ...mapState(['address', 'categorys']),
      ...mapState({
        // address: 'msite.address'
        address: state => state.msite.address,  // statevuex
        categorys: state => state.msite.categorys,
      }),

      /*
      
      8
       */
      categorysArr () {
        const {categorys} = this
        const bigArr = []
        let smallArr = []

        // 
        categorys.forEach(c => {

          //  ()
          if(smallArr.length===0) { // 
            bigArr.push(smallArr)

          }

          // c
          smallArr.push(c)

          // 8
          if (smallArr.length===8) { // , 
            smallArr = []
          }

        })

        return bigArr
      }
    },

    /*
    : swiper
    ()
    : watch + $nextTick(callback)  callbackswiper
    */
    watch: {// 

      //   ==>  ==> 
      /*categorys () { // categorys
        //  DOM  DOM 
        this.$nextTick(() => {
          new Swiper('.swiper-container', { // 
            loop: true, // 
            // 
            pagination: {
              el: '.swiper-pagination',
            },
          })
        })
      }*/
    },

    components: {
      ShopList
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixins.styl"

  .msite  //
    width 100%
    .msite_nav
      bottom-border-1px(#e4e4e4)
      margin-top 45px
      height 200px
      background #fff
      .swiper-container
        width 100%
        height 100%
        .swiper-wrapper
          width 100%
          height 100%
          .swiper-slide
            display flex
            justify-content center
            align-items flex-start
            flex-wrap wrap
            .link_to_food
              width 25%
              .food_container
                display block
                width 100%
                text-align center
                padding-bottom 10px
                font-size 0
                img
                  display inline-block
                  width 50px
                  height 50px
              span
                display block
                width 100%
                text-align center
                font-size 13px
                color #666
        .swiper-pagination
          >span.swiper-pagination-bullet-active
            background #02a774
</style>


